import Pizzablock from "../PizzzaBlock";
import Sort from "../Sort";
import Categories from "../Categories";
import LoadingBlock from "../PizzzaBlock/LoadingBlock";
import { useEffect, useRef, useCallback } from "react";
import Pagination from "../Pagination";

import { useSelector } from "react-redux";
import {
  setCategoryId,
  setPageCount,
  selectFilter,
} from "../../Redux/Slices/filterSlice";

import qs from "qs";
import { useNavigate } from "react-router-dom";
import {
  fetchPizzas,
  selectPizzaData,
  SearchPizzaParams,
} from "../../Redux/Slices/pizzaSlice";
import { useAppDispatch } from "../../Redux/store";

const Home: React.FC = () => {
  const navigate = useNavigate();
  const { sortType, categoryId, pageCount, searchValue } =
    useSelector(selectFilter);

  const { items, status } = useSelector(selectPizzaData);
  const dispatch = useAppDispatch();
  const isSearch = useRef(false);
  const isMounted = useRef(false);

  const onChangeCategory = useCallback((id: number) => {
    dispatch(setCategoryId(id));
  }, [])

  const onChangePage = (value: number) => {
    dispatch(setPageCount(value));
  };

  const getPizzas = async () => {
    const order = sortType.sort.includes("-") ? "asc" : "desc";
    const sortBY = sortType.sort.replace("-", "");
    const category = categoryId > 0 ? `category=${categoryId}` : "";
    const search = searchValue ? `search=${searchValue}` : "";
    dispatch(
      fetchPizzas({
        order,
        sortBY,
        category,
        search,
        pageCount: String(pageCount),
      })
    );
  };

  // useEffect(() => {
  //   if (isMounted.current) {
  //     const queryString = qs.stringify({
  //       sort: sortType.sort,
  //       categoryId,
  //       pageCount,
  //     });
  //     navigate(`?${queryString}`);
  //   }
  //   isMounted.current = true;
  // }, [categoryId, sortType, pageCount]);

  // useEffect(() => {
  //   if (window.location.search) {
  //     const params = qs.parse(
  //       window.location.search.substring(1)
  //     ) as unknown as SearchPizzaParams;
  //     console.log(params.category);
  //     console.log(qs.parse(window.location.search.substring(1)));

  //     const sort = sortTypes.find((obj) => obj.sort === params.sortBY);

  //     dispatch(
  //       setFilters({
  //         searchValue: params.search,
  //         categoryId: Number(params.category),
  //         pageCount: Number(params.pageCount),
  //         sortType: sort || sortTypes[0],
  //       })
  //     );
  //     isSearch.current = true;
  //   }
  // }, []);

  useEffect(() => {
    if (!isSearch.current) {
      getPizzas();
    }
    isSearch.current = false;
    window.scrollTo(0, 0);
  }, [categoryId, sortType, searchValue, pageCount]);

  const pizzas = items.map((obj: any, i: number) => (
    <Pizzablock key={i} {...obj} />
  ));
  const skilletons = Array(12)
    .fill(0)
    .map((_, index) => <LoadingBlock key={index} />);

  return (
    <div className="container">
      <div className="content__top">
        <Categories onChangeCategory={onChangeCategory} value={categoryId} />
        <Sort />
      </div>
      <h2 className="content__title">Все пиццы</h2>
      {status === "Error" ? (
        <div className="content__error-info">
          <h2>Произошла ошибка 😕</h2>
          <p>
            К сожалению, не удалось получить пиццы. Попробуйте повторить попытку
            позже
          </p>
        </div>
      ) : (
        <div className="content__items">
          {status === "Loading" ? skilletons : pizzas}
        </div>
      )}

      <Pagination onChangePage={onChangePage} />
    </div>
  );
};

export default Home;

