import { useRef, useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  SortPropertyEnum,
  selectSort,
  setSort,
} from "../../Redux/Slices/filterSlice";


type SortItem = {
  name: string;
  sort: SortPropertyEnum;
};

export const sortTypes: SortItem[] = [
  { name: `популярности (DESC)`, sort: SortPropertyEnum.RATING_DESC },
  { name: "популярности (ASC)", sort: SortPropertyEnum.RATING_ASC },
  { name: "цене (DESC)", sort: SortPropertyEnum.PRICE_DESC },
  { name: "цене (ASC)", sort: SortPropertyEnum.PRICE_ASC },
  { name: "алфавиту (DESC)", sort: SortPropertyEnum.TITLE_DESC },
  { name: "алфавиту (ASC)", sort: SortPropertyEnum.TITLE_ASC },
];

const Sort = () => {
  const dispatch = useDispatch();
  const sortType = useSelector(selectSort);
  const [open, setOpen] = useState(false);

  const sortRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    document.body.addEventListener("click", handleOudSide);
    return () => {
      document.body.removeEventListener("click", handleOudSide);
    };
  }, []);

  const handleOudSide = (e: any) => {
    const path = e.composedPath();
    if (sortRef.current && !path.includes(sortRef.current)) {
      setOpen(false);
    }
  };

  const handleActiveClass = (obj: SortItem) => {
    dispatch(setSort(obj));
    setOpen(false);
  };

  const openPop = () => setOpen(!open);

  return (
    <div ref={sortRef} className="sort">
      <div className="sort__label">
        <svg
          width="10"
          height="6"
          viewBox="0 0 10 6"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
            fill="#2C2C2C"
          />
        </svg>
        <b>Сортировка по:</b>
        <span onClick={openPop}>{sortType.name}</span>
      </div>
      {open && (
        <div className="sort__popup">
          <ul>
            {sortTypes.map((obj, index) => (
              <li
                onClick={() => handleActiveClass(obj)}
                className={sortType.sort === obj.sort ? "active" : ""}
                key={obj.name}
              >
                {obj.name}
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
};

export default Sort;
